import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";
import { Redirect } from "react-router-dom";

import PaymentCategoryUpdateForm from "./updateForm";

import notification from "../../../../components/notification";
import { Spin } from "antd";

import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/payment/paymentCategory/actions";
import { Box } from "../../../App/app.style";

const redirectTo = "/dashboard/setup/payment-categories";

class UpdatePaymentCategory extends Component {
  // Get All data diawal
  componentDidMount() {
    const { editPaymentCategory } = this.props;
    const { id } = this.props.match.params;
    editPaymentCategory(id);
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.paymentCategory.error) {
      notification("error", this.props.paymentCategory.error);
      this.props.clearError();
    }

    // Display success notif
    if (this.props.paymentCategory.success) {
      this.props.history.replace(redirectTo)
    }
  }

  handleSubmit = value => {
    const { updatePaymentCategory } = this.props;
    const { id } = this.props.match.params;

    // Uppercasing location code
    //  value.location_code = value.location_code.toUpperCase();
    updatePaymentCategory(id, value);
  };

  render() {
    // console.log(this.props);
    return (
      <LayoutWrapper>
        <Box>
          <PageHeader title={<IntlMessages id="paymentCategories.update.title" />}
            style={{ margin: 0 }}
          >
            <Button onClick={() => this.props.history.replace(redirectTo)}>
              <IntlMessages id="button.back" />
            </Button>
          </PageHeader>

          {this.props.paymentCategory.loadingEdit ? (
            <div className="spinStyle">
              <Spin size="large" />
            </div>
          ) : (
              <PaymentCategoryUpdateForm
                auth={this.props.auth}
                submit={this.handleSubmit}
                paymentCategory={this.props.paymentCategory}
              />
            )}
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return {
    auth: state.auth,
    paymentCategory: state.paymentCategory
  };
};

export default compose(
  connect(
    mapStateToProps,
    actions
  )
)(UpdatePaymentCategory);
