import React, { Component } from "react";

import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import {
  Alert,
  // Checkbox,
  Col,
  Empty,
  Form,
  Input,
  Select,
  Spin,
  Radio,
  Row
} from "antd";

const { Option } = Select;
// const { TextArea } = Input;

class PaymentCategoryUpdateForm extends Component {
  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        // console.log("values", values);
        this.props.submit(values);
      }
    });
  };

  render() {
    // console.log(this.props);
    const { getFieldDecorator, getFieldError } = this.props.form;
    const { paymentCategory } = this.props;
    const { accounts, cash_banks, loading, locations } = paymentCategory
      ? paymentCategory
      : [];
    const data = paymentCategory.payment_category
      ? paymentCategory.payment_category
      : [];
    const used = data.is_used;
    const error = paymentCategory.errorField ? paymentCategory.errorField : [];
    // console.log(data);

    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };

    const tailFormItemLayout = {
      wrapperCol: {
        xs: {
          span: 24,
          offset: 0
        },
        sm: {
          span: 24,
          offset: 0
        }
      }
    };

    const radioGroupStyle = {
      display: "flex",
      width: "100%"
    };

    const radioButtonStyle = {
      flex: "1",
      textAlign: "center"
    };

    return (
      <Form layout="vertical" onSubmit={this.handleSubmit}>
        {used ? (
          <Row style={{ marginBottom: "20px" }}>
            <Col span={24}>
              <Alert
                message={<IntlMessages id="paymentCategories.help" />}
                type="warning"
                showIcon
              />
            </Col>
          </Row>
        ) : (
          ""
        )}
        {/* <Divider orientation="left" style={{ fontSize: 14 }}>
          <IntlMessages id="paymentCategories.add.subtitle.data" />
        </Divider> */}
        <div className="isoDetailDataFlex" style={{ padding: "0" }}>
          <div
            className="isoColumn-2 isoFormGroupComp"
            style={{ padding: "0 15px 0 0" }}
          >
            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="paymentCategories.add.code" />}
              validateStatus={
                error.payment_category_code ||
                getFieldError("payment_category_code")
                  ? "error"
                  : null
              }
              help={
                error.payment_category_code
                  ? error.payment_category_code
                  : getFieldError("payment_category_code")
              }
            >
              {getFieldDecorator("payment_category_code", {
                initialValue: data.payment_category_code,
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  },
                  {
                    max: 30,
                    message: <IntlMessages id="maxLength30" />
                  }
                ]
              })(
                <Input
                  disabled={used ? true : false}
                  //style={{ textTransform: "uppercase" }}
                />
              )}
            </Form.Item>

            <Form.Item {...formItemLayout} label={<IntlMessages id="name" />}>
              {getFieldDecorator("description", {
                initialValue: data.description,
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  },
                  {
                    max: 50,
                    message: <IntlMessages id="maxLength50" />
                  }
                ]
              })(<Input disabled={used ? true : false} />)}
            </Form.Item>

            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="paymentTerms.add.default" />}
            >
              {getFieldDecorator("is_default", {
                initialValue: data.is_default
              })(
                <Radio.Group
                  buttonStyle="solid"
                  style={{ display: "flex", width: "50%" }}
                >
                  <Radio.Button style={radioButtonStyle} value={1}>
                    <IntlMessages id="yes" />
                  </Radio.Button>
                  <Radio.Button style={radioButtonStyle} value={0}>
                    <IntlMessages id="no" />
                  </Radio.Button>
                </Radio.Group>
              )}
            </Form.Item>
          </div>
          <div
            className="isoColumn-2 isoFormGroupComp"
            style={{ padding: "0 0 0 15px" }}
          >
            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="paymentType" />}
            >
              {getFieldDecorator("payment_type", {
                initialValue: data.payment_type,
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  }
                ]
              })(
                <Radio.Group
                  onChange={() => {
                    this.props.form.setFieldsValue({
                      id_account_direct_sales: null
                    });
                  }}
                  buttonStyle="solid"
                  style={radioGroupStyle}
                  disabled={used ? true : false}
                >
                  <Radio.Button style={radioButtonStyle} value={1}>
                    <IntlMessages id="cash" />
                  </Radio.Button>
                  <Radio.Button style={radioButtonStyle} value={2}>
                    <IntlMessages id="credit" />
                  </Radio.Button>
                  <Radio.Button style={radioButtonStyle} value={3}>
                    <IntlMessages id="card" />
                  </Radio.Button>
                  <Radio.Button style={radioButtonStyle} value={4}>
                    <IntlMessages id="other" />
                  </Radio.Button>
                </Radio.Group>
              )}
            </Form.Item>

            {this.props.form.getFieldValue("payment_type") === 1 ||
            this.props.form.getFieldValue("payment_type") === 3 ? (
              <Row>
                <Col span={24}>
                  <Form.Item
                    {...formItemLayout}
                    label={<IntlMessages id="sidebar.location" />}
                  >
                    {getFieldDecorator("id_location", {
                      initialValue: data.id_location,
                      rules: [
                        {
                          required: true,
                          message: <IntlMessages id="required" />
                        }
                      ]
                    })(
                      <Select
                        loading={paymentCategory.loading}
                        showSearch
                        optionFilterProp="children"
                        onChange={() => {
                          this.props.form.setFieldsValue({
                            id_cash_bank: null
                          });
                        }}
                        disabled={used ? true : false}
                      >
                        {locations.map((data, key) => (
                          <Option key={key} value={data.id_location}>
                            {data.location_name}
                          </Option>
                        ))}
                      </Select>
                    )}
                  </Form.Item>
                </Col>
                <Col span={24}>
                  <Form.Item
                    {...formItemLayout}
                    label={<IntlMessages id="sidebar.cashBankReport" />}
                  >
                    {getFieldDecorator("id_cash_bank", {
                      initialValue: data.id_cash_bank,
                      rules: [
                        {
                          required: true,
                          message: <IntlMessages id="required" />
                        }
                      ]
                    })(
                      <Select
                        loading={paymentCategory.loading}
                        showSearch
                        optionFilterProp="children"
                        notFoundContent={
                          paymentCategory.loading ? (
                            <Spin size="small" />
                          ) : this.props.form.getFieldValue("id_location") ? (
                            <Empty description={<IntlMessages id="noData" />} />
                          ) : (
                            <Empty
                              description={<IntlMessages id="noLocation" />}
                            />
                          )
                        }
                      >
                        {cash_banks.map((data, key) => {
                          if (
                            this.props.form.getFieldValue("id_location") ===
                            data.id_location
                          ) {
                            return (
                              <Option key={key} value={data.id_cash_bank}>
                                {data.cash_bank_code +
                                  " (" +
                                  data.description +
                                  ")"}
                              </Option>
                            );
                          }
                        })}
                      </Select>
                    )}
                  </Form.Item>
                </Col>
              </Row>
            ) : (
              <Form.Item
                {...formItemLayout}
                label={<IntlMessages id="accounts.pageHeader" />}
              >
                {getFieldDecorator("id_account_direct_sales", {
                  initialValue: data.id_account_direct_sales,
                  rules: [
                    {
                      required: true,
                      message: <IntlMessages id="required" />
                    }
                  ]
                })(
                  <Select
                    loading={paymentCategory.loading}
                    showSearch
                    optionFilterProp="children"
                    //disabled={used ? true : false}
                  >
                    {accounts.map((data, key) => {
                      if (
                        (this.props.form.getFieldValue("payment_type") === 2 &&
                          data.id_account_type === "03") ||
                        (this.props.form.getFieldValue("payment_type") === 4 &&
                          data.id_account_type !== "02" &&
                          data.id_account_type !== "03")
                      ) {
                        return (
                          <Option key={key} value={data.id_account}>
                            {data.account_code + " (" + data.account_name + ")"}
                          </Option>
                        );
                      }
                    })}
                  </Select>
                )}
              </Form.Item>
            )}
          </div>
        </div>

        <Form.Item {...tailFormItemLayout}>
          <Button
            type="primary"
            htmlType="submit"
            style={{ marginTop: "20px", marginLeft: "50px" }}
            loading={loading}
            // disabled={used ? true : false}
          >
            <IntlMessages id="button.submit" />
          </Button>
        </Form.Item>
      </Form>
    );
  }
}

const WrappedPaymentCategoryUpdateForm = Form.create({
  name: "update_payment_category"
})(PaymentCategoryUpdateForm);

export default WrappedPaymentCategoryUpdateForm;
